<template>
    <div style="background-color:rgb(245, 245, 245);height:565px;">
        <div style="width:100%;height:60px;"></div>
        <div id="main-div1">
            <el-row id="main-row1">
                <h1 ></h1>
            </el-row>
            <div id="info-div">
                <el-row>
                    <!--  -->
                    <el-col :span="12">
                        <el-card>
                            <span class="card-span"></span><br>
                            <span class="info-span">
                                {{name}} &nbsp;&nbsp;
                                <el-button type="text" @click="setNameFormDefault()" ></el-button>
                                <!-- namedialog -->
                                <el-dialog
                                    title=""
                                    :visible.sync="editNameShow"
                                    width="25%"
                                    >
                                    <div>
                                        <el-form :model="nameForm">
                                            <el-form-item prop="uname" label=""  label-width="80px" >
                                                <el-input id="form-name" class="dialog-input" v-model.trim="nameForm.uname" placeholder="">
                                                </el-input>
                                            </el-form-item>
                                        </el-form> 
                                    </div>
                                    <span slot="footer" class="dialog-footer">
                                        <el-button @click="editNameShow=false"></el-button>
                                        <el-button type="warning" @click="handleEditName()"></el-button>
                                    </span>
                                </el-dialog>
                            </span>
                        </el-card>
                        
                    </el-col>
                    <!--  -->
                    <el-col :span="12" :offset="1">
                        <el-card >
                            <span class="card-span"></span><br>
                            <span class="info-span">
                                {{phone}} &nbsp;&nbsp;
                                <el-button type="text" @click="setPhoneFormDefault()" ></el-button>
                                <!-- dialog -->
                                <el-dialog
                                    title=""
                                    :visible.sync="editPhoneShow"
                                    width="25%">
                                    <div>
                                        <el-form :model="phoneForm">
                                            <el-form-item prop="telphone" label="" label-width="80px" >
                                                <el-input  v-model.trim="phoneForm.telphone" placeholder="">
                                                </el-input>
                                            </el-form-item>
                                        </el-form> 
                                    </div>
                                    <span slot="footer" class="dialog-footer">
                                        <el-button @click="editPhoneShow=false"></el-button>
                                        <el-button type="warning" @click="handleEditPhone()"></el-button>
                                    </span>
                                </el-dialog>
                            </span>
                        </el-card>
                    </el-col>
                </el-row>
                <div style="width:100%;height:20px"></div>
                <el-row>
                    <!--  -->
                    <el-col :span="12">
                        <el-card >
                            <span class="card-span"></span><br>
                            <span class="info-span">
                                <span v-if="this.sex===1 || this.sex==='1'"></span>
                                <span v-if="this.sex===0 || this.sex==='0'"></span>
                                &nbsp;&nbsp;
                                <el-button type="text" @click="setSexFormDefault()" ></el-button>
                                <!-- dialog -->
                                <el-dialog
                                    title=""
                                    :visible.sync="editSexShow"
                                    width="25%">
                                    <div>
                                        <el-form :model="sexForm">
                                            <el-form-item prop="sex">
                                                <el-select v-model="sexForm.sex" placeholder="">
                                                     <el-option
                                                        v-for="item in options"
                                                        :key="item.value"
                                                        :label="item.label"
                                                        :value="item.value">
                                                    </el-option>
                                                </el-select>
                                            </el-form-item>
                                        </el-form> 
                                    </div>
                                    <span slot="footer" class="dialog-footer">
                                        <el-button @click="editSexShow=false"></el-button>
                                        <el-button type="warning" @click="handleEditSex()"></el-button>
                                    </span>
                                </el-dialog>
                            </span>
                        </el-card>
                    </el-col>
                    <!--  -->
                    <el-col :span="12" :offset="1">
                        <el-card >
                            <span class="card-span"></span><br>
                            <span class="info-span">
                                {{birthdate}} &nbsp;&nbsp;
                                <el-button type="text" @click="setDateFormDefault()" ></el-button>
                                <!-- dialog -->
                                <el-dialog
                                    title=""
                                    :visible.sync="editBirthdateShow"
                                    width="25%">
                                    <div>
                                        <el-form :model="dateForm">
                                            <el-form-item prop="birthdate">
                                                <el-date-picker
                                                    v-model="dateForm.birthdate"
                                                    type="date"
                                                    placeholder=""
                                                    format="yyyy  MM  dd "
                                                    value-format="yyyy-MM-dd">
                                                </el-date-picker>
                                            </el-form-item>
                                        </el-form> 
                                    </div>
                                    <span slot="footer" class="dialog-footer">
                                        <el-button @click="editBirthdateShow=false"></el-button>
                                        <el-button type="warning" @click="handleEditDate()"></el-button>
                                    </span>
                                </el-dialog>
                            </span>
                        </el-card>
                    </el-col>
                </el-row>
            </div>            
        </div>          
    </div>
</template>
    
<style>
    
    #main-div1 {
        margin: 0 auto;

        width:715px;
        height:450px;
        /* border: 1px solid #111; */

    }

    #main-row1 {
        line-height: 76px;

        height: 76px;
        /* border: 1px solid #f0f; */
    }
    #main-row1 h1{
        font-size: 28px;
        font-weight: 300;
    }

    #info-div {
        width: 100%;
        height: 280px;
        /* background-color: #fff; */
        /* border: 1px solid rgb(54, 64, 211); */
    }

    #info-div > .el-row {
        width: 100%;
        height: 50%;

    }

    #info-div > .el-row > .el-col {
        width: 45%;
        height: 100%;
        /* border: 1px solid red; */
    }
    #info-div > .el-row > .el-col > .el-card {
        height: 100%;
    }

    .card-span {
        /* font-family: 'SimSun'; */
        font-size: 18px;
        font-weight: bold;
        color: #E6A23C;

        display: block;
        margin-top: 20px;
        margin-left: 20px;

        /* border: 1px solid black; */
    }

    .info-span {
        /* font-family: 'FangSong';
        font-size: 16px; */
        text-align: center;
        color: gray;

        display: block;
        margin: 0 auto;

        /* border: 1px solid black; */
    }

    
</style>

<script>
export default {
    data() {
        return {
            id: this.$route.query.id,
            name: '',
            phone: '',
            sex: '',
            birthdate: '',

            editNameShow: false,
            editPhoneShow: false,
            editSexShow: false,
            editBirthdateShow: false,

            options: [{
                value: '1',
                label: ''
            }, {
                value: '0',
                label: ''
            }],

            nameForm:{
                uname: ''
            },
            phoneForm:{
                telphone:''
            },
            sexForm:{
                sex:''
            },
            dateForm:{
                birthdate:''
            },

            

        };
    },
    methods: {
        // 
        handleClose(done) {
            // this.$confirm('')
            // .then(_ => {
            //     done();
            // })
            // .catch(_ => {});
        },
        // 
        setNameFormDefault(){
            this.nameForm.uname = this.name;
            this.editNameShow=true;
        },        
        // 
        setPhoneFormDefault(){
            this.phoneForm.telphone = this.phone;
            this.editPhoneShow=true;
        },
        // 
        setSexFormDefault(){
            this.sexForm.sex = this.sex.toString();
            this.editSexShow=true;
        },
        // 
        setDateFormDefault(){
            this.dateForm.birthdate = this.birthdate;
            this.editBirthdateShow=true;
        },
        // 
        nameVailated(val){
            console.log(val);   
            // unamenull
            if(val === null || val === ""){
                this.$message({
                    message:"",
                    type: "warning"
                })
                return false;
            }
            return true;
        },
        // 
        phoneVailated(val){
            console.log(val);   
            // telphonenull
            if(val === null || val === ""){
                this.$message({
                    message:"",
                    type: "warning"
                })
                return false;
            }else if (val.length != 11) {
                this.$message({
                    message:"",
                    type: "warning"
                })
                return false;
            }
            return true;
        },
        // sex
        sexVailated(val){
            console.log(val)
            // sexnull
            if (val === null || val === "") {
                this.$message({
                    message:"",
                    type: "warning"
                })
                return false;
            }else if (val>1 || val<0) {
                this.$message.error("");
                return false;
            }
            return true;            
        },
        // birthdate
        birthdateVailated(val){
            console.log(val)
            // unamenull
            if(val === null || val === ""){
                this.$message({
                    message:"",
                    type: "warning"
                })
                return false;
            }
            return true;
        },
        // 
        handleEditName(){
            var val = this.nameForm.uname;
            if(this.nameVailated(val)){
                console.log("")
                this.axios.post("api/setName",{
                    username: val,
                    id: this.id
                })
                .then(res => {
                    console.log(res)
                    if (res.status === 200) {
                        if (res.data.msg === "same"){
                            this.$message({
                                message:"",
                                type: "warning"
                            })
                            this.editNameShow = true;
                        }else if (res.data.msg === "success") {
                            this.name = res.data.username;
                            this.nameForm.uname = res.data.username;
                            this.$message({
                                message:"",
                                type: "success"
                            })
                            this.editNameShow = false;
                        }else if(res.data.msg === "error"){
                            this.$message.error("");
                            this.editNameShow = false;
                        }else if(res.data.msg === "fail"){
                            this.$message({
                                message:"",
                                type: "warning"
                            })
                            this.editNameShow = true;
                        }
                    }
                })
                .catch(err => {
                    console.error(err); 
                })
            }else{
                this.editNameShow = true;
            }
        },
        // 
        handleEditPhone(){
            var val = this.phoneForm.telphone;
            if (this.phoneVailated(val)) {
                console.log("")
                this.axios.post("/api/setTel",{
                    id: this.id,
                    telephone: val
                })
                .then(res => {
                    console.log(res)
                    if (res.status === 200) {
                        if (res.data.msg === "same") {
                            this.$message({
                                message:"",
                                type: "warning"
                            })
                            this.editPhoneShow = true;
                        }else if (res.data.msg === "success") {
                            this.phone = res.data.phone;
                            this.phoneForm.telphone = res.data.phone;
                            this.$message({
                                message:"",
                                type: "success"
                            })
                            this.editPhoneShow = false;
                        }else if (res.data.msg === "error") {
                            this.$message.error("");
                            this.editPhoneShow = false;
                        }else if (res.data.msg === "fail") {
                            this.$message({
                                message:"",
                                type: "warning"
                            })
                            this.editPhoneShow = true;
                        }
                    }
                })
                .catch(err => {
                    console.error(err); 
                })
            }else{
                this.editPhoneShow = true;
            }
        },
        // 
        handleEditSex(){
            var val = this.sexForm.sex;
            if (this.sexVailated(val)) {
                console.log("sex")
                this.axios.post("/api/setSex",{
                    id: this.id,
                    sex: val
                })
                .then(res => {
                    console.log(res)
                    if (res.status === 200) {
                        if (res.data.msg === "same") {                            
                            this.$message({
                                message:"",
                                type: "warning"
                            })
                            this.editSexShow = true;
                        }else if (res.data.msg === "success") {
                            this.sex = res.data.sex;
                            this.sexForm.sex = res.data.sex;
                            this.$message({
                                message:"",
                                type: "success"
                            })
                            this.editSexShow = false;
                        }else if (res.data.msg === "error") {
                            this.$message.error("");
                            this.editSexShow = true;
                        }else if (res.data.msg === "fail") {
                            this.$message({
                                message:"sex",
                                type: "warning"
                            })
                            this.editSexShow = true;
                        }
                    }
                })
                .catch(err => {
                    console.error(err); 
                })
                
            }else{
                this.editSexShow = true;
            }
        },
        // 
        handleEditDate(){
            var val = this.dateForm.birthdate;
            if (this.birthdateVailated(val)) {
                console.log("")
                this.axios.post("/api/setBirthdate",{
                    id: this.id,
                    birthdate: val
                })
                .then(res => {
                    console.log(res)
                    if (res.status === 200) {
                        if (res.data.msg === "same") {                            
                            this.$message({
                                message:"",
                                type: "warning"
                            })
                            this.editBirthdateShow = true;
                        }else if (res.data.msg === "success") {
                            this.birthdate = res.data.birthdate;
                            this.dateForm.birthdate = res.data.birthdate;
                            this.$message({
                                message:"",
                                type: "success"
                            })
                            this.editBirthdateShow = false;
                        }else if (res.data.msg === "error") {
                            this.$message.error("");
                            this.editBirthdateShow = true;
                        }else if (res.data.msg === "fail") {
                            this.$message({
                                message:"date",
                                type: "warning"
                            })
                            this.editBirthdateShow = true;
                        }
                    }

                })
                .catch(err => {
                    console.error(err); 
                })
            }
        },
        // 
        loadUserInfo(){
            this.axios.post("api/userInfo",{id:this.id})
            .then(res => {
                console.log(res)
                if (res.status === 200) {
                    var user = res.data.user;
                    this.name = user.username;
                    this.phone = user.telephone;
                    this.birthdate = user.birthdate;
                    this.sex = user.sex;
                }
            })
            .catch(err => {
                console.error(err); 
            });
        }
    },
    // 
    mounted: function(){
        this.loadUserInfo();
        document.title = "";
    }
};
</script>


